import uniCollapse from '@/colorui/components/uni-collapse.vue'
import uniCollapseItem from '@/colorui/components/uni-collapse-item.vue'
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			info: {},
			list: [],
			showModal: false,
			stars: [1, 2, 3, 4, 5],
			para:{
				dispatch_id: 0,
				appraise_grade: 0,
				appraise_contnet: ""
			}
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.info = info;
		//获取派车单
		if (this.info.status >= 2) {
			global.$http.post('/car/dispatch/getListByDispatch', {
				params: {
					order_code: this.info.order_code
				},
			}).then(res => {
				if (res.status === "0") {
					this.list = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	},
	methods: {
		getCarListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchCarList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.cars = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getDriverListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchDriverList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.drivers = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getTitle: function(totalFee) {
			return "总费用(元)：" + totalFee;
		},
		getStatusStr: function(status) {
			return misEnum.DispatchRecordEnumMap.get(status);
		},
		appraise: function(order){
			this.para.dispatch_id = order.id;
			this.para.appraise_grade = 0;
			this.para.appraise_contnet = "";
			this.showModal = true;
		},
		hideModal: function(){
			this.showModal = false;
		},
		fillAppraise: function(e){
			this.para.appraise_contnet = e.detail.value;
		},
		chooseStar: function(e) { //点击评星
		    this.para.appraise_grade = e;
		},
		sureModal:function(){
			if(this.para.appraise_grade <= 0){
				uni.showToast({
					title: "请评分",
					icon: 'none'
				});
				return;
			}
			this.showModal = false;
			//提交评分内容
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/car/dispatch/saveAppraise', {
				params: this.para,
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						icon: 'none',
						title: '提交成功'
					});
					//设置评价状态
					this.list.forEach(c=>{
						if(c.id == this.para.dispatch_id){
							c.is_appraise = 1;
							c.appraise_grade = this.para.appraise_grade;
							c.appraise_contnet = this.para.appraise_contnet;
						}
					})
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	}
}
